/* {default} */
@import url('https://fonts.googleapis.com/css2?family=Alumni+Sans+Pinstripe&family=Caveat+Brush&family=Fasthand&family=Montserrat:wght@300&family=Open+Sans:wght@300&family=Poppins:ital,wght@0,400;1,500&family=Quicksand&family=Ruda&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
font-family: 'Alumni Sans Pinstripe', sans-serif;
font-family: 'Caveat Brush', cursive;
font-family: 'Fasthand', cursive;
font-family: 'Montserrat', sans-serif;
font-family: 'Open Sans', sans-serif;
font-family: 'Poppins', sans-serif;
font-family: 'Quicksand', sans-serif;
font-family: 'Ruda', sans-serif;
}

.App{
  background-color:lightgrey;
  display: flex;
  height: 100vh;
  justify-content: center;
  align-items: center;
}
.App-container{
    display: grid;
    height: 93%;
    width: 94%;
    border-radius: 1rem;
    background-color: rgb(214, 214, 248);
    overflow: hidden;
    grid-template-columns: 11rem auto auto auto;
    column-gap: 1rem

}
/* {sidebar} */

.sidebar{
    display: flex;
    flex-direction: column;
    position: relative;
    padding-top: 3rem;
    overflow: hidden;
    background-color: white;
    height: 100%;
   
}

.fa-slack{
    position: absolute;
    top:25px;
    left:10px;
}
.menu{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    margin: 25px auto 0 auto;
    justify-content: center;

}
.menu-item{
    display: flex;
    align-items: center;
    gap: 1rem;
    position: relative;
    transition: all 300ms ease;
    font-size: 14px;
    height: 0.7rem;
    padding: 15px 10px;
    border-radius: 8px;
}
.b-div{
    margin-top: 9rem;
    display: flex;
    flex-direction: column;
    gap:1rem;
}
.menu-item:hover{
    cursor: pointer;
    background-color:  rgb(197, 197, 252);
}
.active{
    background-color:  rgb(197, 197, 252);
    margin-left: 0;
}


/* {  sidecomp1  } */
.sidecomp1{
    margin: auto;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 8px;
    overflow: hidden;
    height: 80%;
}
.description{
    display: flex;
    flex-direction: column;
    gap: 8px;
    height: 200px;
    width: 200px;
}
.description h2{
    font-weight: 200;
}
.description p{
    font-size: 12px;
    line-height: 20px;
}
.image-1{
    width: 260px;
    height: 200px;
    overflow: hidden;
    border-radius: 20px;
}
.image-1 img{
    width: 100%;
    height: 100%;
}
.card{
    width: 270px;
    height: 270px;
    background-color: blue;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap:20px;
    padding:20px 20px;
    color: white;
    border-radius: 30px;
    border: none;
}
.card>i{
    border: 1px solid white;
    background-color: white;
    color: blue;
    width: 30px;
    height: 40px;
    border-radius: 50%;
    padding: 11px 5px;
}
.card h5{
    font-size: 18px;
}
.card p{
    font-size: 10px;
    line-height: 15px;
}
.card button{
    padding: 10px 20px;
    background-color: white;
    color: black;
    border: none;
    border-radius: 15px;
}
.card button:hover{
    background-color: black;
    color: white;
}

/* {  sidecomp2  } */
 

.Sidecomp2{
 display: flex;
 flex-direction: column;
 overflow: hidden;
 align-items: center;
 gap:15px;
 margin: auto;
 height: 80%;
}

.show-div{
    display: flex;
    flex-direction: column;
    gap:15px;
}
.likes{
    display: flex;
    width: 280px;
    height: 60px;
    background-color: white;
    gap:1rem;
    align-items: center;
    padding: 0 10px;
    border-radius: 20px;
    position: relative;
}
.fa-thumbs-up{
    color: white;
    background-color: blue;
    padding: 8px;
    border-radius: 50%;
}
.fa-heart{
    color: white;
    background-color: red;
    padding: 8px;
    border-radius: 50%;
}
.fa-face-smile{
    color: white;
    background-color: yellow;
    padding: 8px;
    border-radius: 50%;
}
.icon{
    position: absolute;
    right: 25px;
}
.fa-caret-up{
    color: aqua;
}
.fa-caret-right{
    color: grey;
}
.fa-caret-down{
    color: orangered;
}
.likes h5{
    font-size: 16px;
}
.likes p{
    color: gray;
    font-size: 12px;
}

.charts{
    height: 300px;
    width: 280px;
    display: flex;
    flex-direction: column;
    background-color: white;
    gap:10px;
    border-radius: 20px;
}
.charts img{
    width: 100%;
    height: 60%;
    border-radius: 20px;
}
.view{
    display: flex;
    padding: 20px 20px;
    background-color: rgb(211, 210, 210);
    justify-content: space-around;
    align-items: center;
    width: 90%;
    margin: 5px auto;
    border-radius: 15px;
    overflow: hidden;
}
.view:hover{
    background-color: darkgrey;
}
.view p{
    font-size: 14px;
    color: rgb(97, 97, 97);
}

.fa-table-list{
    color: rgb(97, 97, 97);
    background-color: white;
    padding: 8px;
    border-radius: 50%;   
}

/* { sidecomp3} */


.Sidecomp3{
   display: flex;
   flex-direction: column;
   margin: auto;
   position: relative;
   height: 80%;
}
.notification{
    position: absolute;
    top:-40px;
    right:-30px;
}
.i-1{
    position: absolute;
    top: -6px;
    right: -2px;
    border: 1px solid red;
    background-color: red;
    height: 5px;
    width: 5px;
    border-radius: 50%;
    color: red;

}

.targets-div{
    display: flex;
    flex-direction: column;
    height: 220px;
    background-color: white;
    gap:1rem;
    padding: 10px;
    border-radius: 20px;
    
}
.targets-div h4{
    margin-top: 20px;
    font-size: 20px;
}

li{
    list-style-type:none ;
}
.progress-div li:nth-of-type(2){
    margin: 20px 0;
}
.bar1{
    margin-top: 5px;
    display: block;
    height: 10px;
    border: 1px solid rgba(0,0,0,0.3);
    overflow: hidden;
    border-radius: 10px;
    background-color: lightblue;
}
.bar2{
    margin-top: 5px;
    display: block;
    height: 10px;
    border: 1px solid rgba(0,0,0,0.3);
    overflow: hidden;
    border-radius: 10px;
    background-color: rgb(248, 248, 151);
}
.bar3{
    margin-top: 5px;
    display: block;
    height: 10px;
    border: 1px solid rgba(0,0,0,0.3);
    overflow: hidden;
    border-radius: 10px;
    background-color: rgb(245, 147, 147);
}
.bar1 span{
    height: 15px;
    float: left;
}
.bar2 span{
    height: 15px;
    float: left;
}
.bar3 span{
    height: 15px;
    float: left;
}
.views{
    width:75%;
    background-color: blue;
}
.followers{
    width: 50%;
    background-color: yellow;
}
.income{
    width:25%;
    background-color: red;
}
.span1{
    position: absolute;
    right: 15px;
    font-size: 12px;
    color: darkgray;
}
li{
    font-size: 14px;
    color: rgb(79, 77, 77);
}
.meetings-div{
    margin-top: 15px;
}
.meet-p{
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
}
.meets{
    display: flex;
    width: 250px;
    height: 60px;
    background-color: white;
    gap:1rem;
    align-items: center;
    padding: 0 10px;
    border-radius: 20px;
    position: relative;
}
.fa-plus{
    margin: 0 auto;
}
.meets-last{
    height: 40px;
    background-color: white;
    border-radius: 20px;
    display: flex;
    align-items: center;
    width: 250px;
}
.meets-last:hover{
    background-color: gray;
    color: white;
}
.img-container{
    width: 40px;
    height: 40px;
   
}
.img{
    width: 100%;
    height: 100%;
    border-radius: 50%;
}
.meetings-div h3{
    margin: 10px 0;
    font-size: 16px;
}
.menu-btn{
    display: none;
}

/* Responsive */
@media  screen and (max-width:1200px) {
    .App-container{
        grid-template-columns: 10% auto auto auto;
        overflow-y: scroll;
    }
    .menu-item>span{
      display: none;
   }
    .logo>i{
     margin: 10px 40px;
    }
    .notification{
        display: none;
    }
    
}
 @media (max-width:768px){
    .App{
        height: auto;
    }
    .sidebar{
        position: fixed;
        z-index: 9;
        width: 50%;
        padding: auto;
    }
    .menu{
        margin-top:20px;
        position: relative;
    }
    .menu-item>span{
        display: block;
    }
    .logo{
        position: absolute;
        left:-20px;
        top:-10px;
    }
    .menu-btn{
        display: flex;
        position: fixed;
        top:25px;
        z-index: 9;
    }
  
    .App-container{
      grid-template-columns:1fr;
      overflow-y: scroll;
    }
    .sidecomp1{
        height: 100%;
        width: 100%;
        gap:10px; 
        align-items: center;
    }
    .Sidecomp2{
        margin: 20px 0;
        height: 100%;
        width: 100%;
        
    }
    .Sidecomp3{
        height: 100%;
        width: 100%;
        position: relative;
        margin-bottom: 20px;
    }
    .description{
        width: 80%;
        margin-top: 20px;
        margin-bottom: -25px;
    }
    .image-1{
        margin-top: -15px;
        width: 80%;
    }
    .card{
        width: 80%;
        height: 220px;
    }
    .notification{
      display: none;
    }
    .show-div{
        width: 100%;
    }
    .likes{
        width: 80%;
        margin: 0 auto;
    }
    .charts{
        width: 80%;
        height: 350px;
    }
    .targets-div{
        width: 80%;
        margin: 0 auto;
    }
    .span1{
        position: absolute;
        right: 70px;
    }
    .meets{
        width: 80%;
        margin: 0 auto;
    }
    .meets-last{
        width: 80%;
        margin: 0 auto;
    }
    .meetings-div h3{
        text-align: center;
    }
}